import React, {useEffect, useState} from 'react';
import style from "./App.module.scss"
import {Header} from "../A1_Header/Header";
import {BurgerMenu} from "../A2_BurgerMenu/BurgerMenu";
import {Home} from "../B0_Home/Home";
import {Route, Routes} from 'react-router-dom';
import {FiberIntroduction} from "../B1_FiberIntroduction/FiberIntroduction";
import {SolarSystem} from "../B2_SolarSystem/SolarSystem";
import {CameraPosition} from "../B3_CameraPosition/CameraPosition";
import {Texture} from "../B4_Texture/Texture";
import {MovingArray} from "../B5_MovingArray/MovingArray";
import {Spiral} from "../B6_Spiral/Spiral";
import {RotateByMouse} from "../B7_RotateByMouse/RotateByMouse";
import {BufferPoints} from "../B8_BufferPoints/BufferPoints";
import {TargetRender} from "../B9_TargetRender/TargetRender";
import {RotateByScroll} from "../B10_RotateByScroll/RotateByScroll";
import {throttle} from "../../helpers/throttle";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {ThreeMFLoaderComponent} from "../B11_ThreeMFLoader/ThreeMFLoader";

export const routes = [
    {
        path: "/",
        element: <Home/>,
        label: "",
    },
    {
        path: "/fiberIntroduction",
        element: <FiberIntroduction/>,
        label: "Fiber Introduction",
    },
    {
        path: "/solarSystem",
        element: <SolarSystem/>,
        label: "Solar System",
    },
    {
        path: "/cameraPosition",
        element: <CameraPosition/>,
        label: "Camera Position",
    },
    {
        path: "/texture",
        element: <Texture/>,
        label: "Texture",
    },
    {
        path: "/moving-array",
        element: <MovingArray/>,
        label: "Moving Array",
    },
    {
        path: "/spiral",
        element: <Spiral/>,
        label: "Spiral",
    },
    {
        path: "/rotateByMouse",
        element: <RotateByMouse/>,
        label: "Rotate by mouse",
    },
    {
        path: "/bufferPoints",
        element: <BufferPoints/>,
        label: "Buffer Geometry - Points - Circle waves",
    },
    {
        path: "/renderTarget",
        element: <TargetRender/>,
        label: "Render Target",
    },
    {
        path: "/rotateByScroll",
        element: <RotateByScroll/>,
        label: "Positioned 3D on page",
    },
    {
        path: "/threeMFLoader",
        element: <ThreeMFLoaderComponent/>,
        label: "3MF loader",
    },
];

export const App = observer(() => {
    const {appStore: {setPageYOffset}} = useStore();

    const onScrollHandler = () => {
        //console.log(window.pageYOffset)
        setPageYOffset(window.pageYOffset);
    }
    useEffect(() => {
        document.addEventListener(
            'scroll',
            throttle(onScrollHandler, 10)
        );
        return document.removeEventListener(
            'scroll',
            throttle(onScrollHandler, 10)
        );
    });


    return (
        <div className={style.app}>
            <Header/>
            <BurgerMenu/>
            <main className={style.main}>
                <Routes>
                    {
                        routes.map(({path, element}, index) => (
                            <Route key={index}
                                   path={path}
                                   element={element}
                            />
                        ))
                    }
                </Routes>
            </main>
        </div>
    );
})

